@import "./variables.scss";

$light-blue: #F6FAFF;
$text: #202F68;
$light-text: #656f96;
$accent-blue: #1942F5;

.cover-container {
    background: $light-blue;
    background-image: url("./assets/airpals/background.png");
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 70px;
}

.cover img {
    position: absolute;
    z-index: -1;
}

.cover-title {
    height: 144px;
    padding-right: 20vw;
}

.cover-img {
    height: 240px;
    width: auto;
}

.project-intro {
    color: $text;
}

.project-text {
    color: $text;
}

.summary {
    background-color: $light-blue;
}

h3 {
    color: $text;
    font-weight: 600;
    padding-bottom: 8px;
}

h2 {
    color: $text;
}

.project-text h4 {
    font-weight: 600;
    color: $text;
    padding: 4px 0 0 0;
}

ol, ul {
    padding: 4px 0 0 36px !important;
}

li {
    padding-bottom: 4px;
}

.block {
    background-color: $light-blue;
}

.threegrid-box {
    background-color: $light-blue;
    font-weight: 500;
}

.big-numbers {
    color: $accent-blue;
    font-weight: 600;
}

.primary {
    display: flex;
    height: 46px;
    width: 240px;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    padding: 12px 20px;
    background-color: $accent-blue;
    color: #ffffff;
    border: none;
    border-radius: 8px;
    font-size: 1.125rem;
    font-weight: 500;
}


.caption {
    color: $light-text;
}

.project-lgimg {
    background-color: $light-blue;
}